import React, { useEffect, useState } from 'react';
import "./table.css";
import { Helmet } from "react-helmet";
import Report from '../components/reports/Report';
import UploadVideo from '../components/upload-video/UploadVideo';
import VideoGallery from './VideoGallery';
import Playlist from './playlist';
import axios from 'axios';
import { useSelector } from 'react-redux';
import Newplaylist from '../components/playlist/Newplaylist.js';
import { PlaylistsTable } from '../components/playlist/PlaylistsTable.js';
import { useLocation, useNavigate } from 'react-router-dom';
import VideoSettingsTabs from '../components/video-settings/VideoSettingsTabs.js';
import PlaylistEdit from './PlaylistEdit.js';

const SSVpage = ({ state, setState, tab, setTab }) => {
    const [playlists, setPlaylists] = useState([]);
    const user = useSelector(state => state.auth.user);
    const location = useLocation();
    const searchParams = new URLSearchParams(location.search);
    const createTable = searchParams.get('createTable');
    const Tableedit = searchParams.get('Tableedit');
    // const [tab, setTab] = useState('video');

    const getPlalists = async () => {
        try {
            const res = await axios.get(`https://liveshopping.asia/getPlaylistByBrandId?brandId=${user.brand_id}`);
            if (res.data.playlist) {
                setPlaylists(res.data.playlist);
            } else {
                setPlaylists([]);
            }
        } catch (error) {
            console.log(error);
        }
    };

    useEffect(() => {
        getPlalists();
    }, [user]);

    const navigate = useNavigate();

    const setCreateTable = (value) => {
        searchParams.set('createTable', value);
        navigate({ search: searchParams.toString() });
    };

    const setTableedit = (value) => {
        searchParams.set('Tableedit', value);
        navigate({ search: searchParams.toString() });
    };

    useEffect(() => {
        // Add or remove a class based on the tab state
        const tabs = document.querySelectorAll('#responsive-tabs-ssv .nav-link');
        tabs.forEach(t => {
            if (t.getAttribute('aria-controls') === tab) {
                t.classList.add('show');
                t.classList.add('active');
            } else {
                t.classList.remove('show');
                t.classList.remove('active');
            }
        });


    }, [tab]);

    // useEffect(() => {

    //     return () => {
    //         setTab('video')
    //     }
    // }, [])

    useEffect(() => {
        setTab('Reports')
    }, [])
    return (
        <>
            <Helmet>
                <title>SWIRL | Short Videos</title>
            </Helmet>
            <div>
                <div className="col">
                    <h4 className='ms-2 p-1' style={{ fontSize: "32px", fontWeight: "700" }}>Shoppable video</h4>
                    <div className='d-flex '>

                        <ul className="nav nav-tabs ssv" id="responsive-tabs-ssv" role="tablist">
                            {/* 
                            <li className="nav-item" role="presentation">
                                <button className="nav-link show active" id="video-tab" data-bs-toggle="tab" data-bs-target="#video" type="button" role="tab" aria-controls="video" aria-selected="true" onClick={() => { setCreateTable(false); setTableedit(false); setTab('video'); }} >Video gallery</button>
                            </li> */}
                            {/* <li className="nav-item" role="presentation">

                                <button className="nav-link" id="Upload-tab" data-bs-toggle="tab" data-bs-target="#Upload" type="button" role="tab" aria-controls="Upload" aria-selected="false" onClick={() => { setCreateTable(false); setTableedit(false); setTab('Upload'); }}>Upload video</button>
                            </li>
                            <li className="nav-item" role="presentation">
                                <button className="nav-link" id="playlist-tab" data-bs-toggle="tab" data-bs-target="#playlist" type="button" role="tab" aria-controls="playlist" aria-selected="false" onClick={() => { setCreateTable(false); setTableedit(false); setTab('playlist'); }}>Playlist</button>
                            </li> */}

                            <li className="nav-item show active" role="presentation">
                                <button className="nav-link" id="Reports-tab" data-bs-toggle="tab" data-bs-target="#Reports" type="button" role="tab" aria-controls="Reports" aria-selected="false" onClick={() => { setCreateTable(false); setTableedit(false); setTab('Reports'); }}>Reports</button>
                            </li>
                            {/* <li className="nav-item" role="presentation">
                                <button className="nav-link" id="Settings-tab" data-bs-toggle="tab" data-bs-target="#Settings" type="button" role="tab" aria-controls="Settings" aria-selected="false" onClick={() => { setTab('Settings'); }}>Settings</button>
                            </li> */}
                        </ul>
                    </div>
                    <div className="tab-content p-3 " id="responsive-tabs-content" style={{
                        height: tab == "Upload" || tab == "Reports" || tab == "playlist" ? 'calc(98vh - 160px)' : 'calc(97vh - 138px)', overflow: tab != "Settings" ? "auto" : ""
                    }}>
                        {/* {tab === 'video' && (

                            <div className="tab-pane fade show active h-100" id="video" role="tabpanel" aria-labelledby="video-tab">

                                <VideoGallery tab={tab} setTab={setTab} />
                            </div>
                        )
                        }
                        {tab === 'Upload' && (
                            <div className="tab-pane fade show active h-100" id="Upload" role="tabpanel" aria-labelledby="Upload-tab">

                                <UploadVideo />
                            </div>
                        )}
                        {tab === 'playlist' && (
                            <div className="tab-pane fade show active h-100" id="playlist" role="tabpanel" aria-labelledby="playlist-tab">
                                {Tableedit === "true" ? (
                                    <PlaylistEdit getPlalists={getPlalists} />
                                ) : (
                                    <>
                                        {createTable === "true" ? (
                                            <Playlist getPlalists={getPlalists} />
                                        ) : (
                                            <>
                                                {playlists.length > 0 ? (
                                                    <PlaylistsTable data={playlists} />
                                                ) : (
                                                    <Newplaylist />
                                                )}
                                            </>
                                        )}
                                    </>
                                )}
                            </div>
                        )} */}
                        {/* {tab === 'Reports' && ( */}
                        <div className="tab-pane fade show active h-100" id="Reports" role="tabpanel" aria-labelledby="Reports-tab">
                            <Report state={state} setState={setState} />
                        </div>
                        {/* )} */}
                        {/* {tab === 'Settings' && (
                            <div className="tab-pane fade show active h-100" id="Settings" role="tabpanel" aria-labelledby="Settings-tab">
                                <VideoSettingsTabs />
                            </div>
                        )} */}
                    </div>
                </div>
            </div >
        </>
    );
}

export default SSVpage;
