import React, { useEffect, useRef, useState } from 'react';
import { Modal } from 'react-bootstrap';
import { db } from '../../firebase-config';
import { getDocs, collection, orderBy, query, addDoc, doc, setDoc, updateDoc, onSnapshot, deleteDoc } from 'firebase/firestore';
import axios from 'axios';
import { useSelector } from 'react-redux';

import "./uploadmodel.css"
import Comment from './Comment';
import PinComment from './PinComment';
import { toast } from 'react-toastify';

const VideoModal = ({ show, handleClose, data }) => {
    const [tab, setTab] = useState("products")
    const [loadingProducts, setLoadingProducts] = useState(false)
    const [products, setProducts] = useState([])
    const user = useSelector(state => state.auth.user);
    const [comments, setComments] = useState([])
    const [message, setMessage] = useState("")
    const [pinMessage, setPinMessage] = useState([])
    const [scrolled, setScrolled] = useState(false);

    const chatEndRef = useRef()
    const scrolldivref = useRef(null);
    const videoId = data.video_id
    const fetchProducts = async () => {
        setLoadingProducts(true)
        try {
            const res = await axios.get(`https://liveshopping.asia/getProductInfoByVideoId?brandId=${user.brand_id}&videoId=${data.video_id}`);
            if (res.data) {
                setProducts(res.data.productResp)
                setLoadingProducts(false)
                scrollToBottom()
            } else {
                setProducts([])
                setLoadingProducts(false)
            }
        } catch (error) {
            console.log(error);
            setProducts([])
            setLoadingProducts(false)
        }
    }

    const fetchData = async (videoId) => {
        try {
            const commentRef = collection(db, 'ssv13_comments', `v${videoId}`, 'comments');
            const q = query(commentRef, orderBy('createdAt', 'asc'));
            const snapshot = await getDocs(q);
            console.log(snapshot);
            const commentsData = snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
            // const cms = commentsData.filter(obj => obj.isRemoved !== 1);
            const objectsWithPin1 = commentsData.filter(obj => obj.isPinned === 1);

            setComments(commentsData)
            setPinMessage(objectsWithPin1)
            setTimeout(() => {
                scrollToBottom()
            }, 100);
        } catch (error) {
            console.error('Error fetching data: ', error);
        }
    };

    const addVideoComment = async (vId, cData) => {
        try {
            const videoDocRef = doc(db, `ssv13_comments/v${vId}`);
            await setDoc(videoDocRef, {});
            const commentDocRef = await addDoc(collection(db, `ssv13_comments/v${vId}/comments`), cData);
            scrollToBottom()
            subComment()
            return commentDocRef.id;
        } catch (error) {
            console.error("Error adding video comment document: ", error);
            throw error;
        }
    };
    const editVideoComment = async (updatedData) => {

        try {
            const commentDocRef = doc(db, `ssv13_comments/v${videoId}/comments/${updatedData.id}`);
            await updateDoc(commentDocRef, updatedData);
            subComment()
        } catch (error) {
            console.error("Error updating video comment document: ", error);
            throw error;
        }
    };

    const scrollToBottom = () => {
        chatEndRef.current?.scrollIntoView({ behavior: 'smooth' });
    };

    const subComment = () => {
        const commentRef = collection(db, 'ssv13_comments', `v${data.video_id}`, 'comments');
        const q = query(commentRef, orderBy('createdAt', 'asc'));

        const unsubscribe = onSnapshot(q, (snapshot) => {
            const commentsData = snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));

            // const cms = commentsData.filter(obj => obj.isRemoved !== 1);
            const objectsWithPin1 = commentsData.filter(obj => obj.isPinned === 1);
            setComments(commentsData);
            setPinMessage(objectsWithPin1);

        }, (error) => {
            console.error('Error fetching data: ', error);
        });
    }

    const handleSubmit = async (e) => {
        e.preventDefault()
        try {
            let cData = {
                uName: user.first_name,
                comment: message,
                createdAt: new Date().getTime(),
                isPinned: 0,
                isRemoved: 0,
                brand_logo: user?.user_profile
            }

            await addVideoComment(data.video_id, cData)
            subComment()
            e.target.reset()
        } catch (error) {
            console.log(error);
        }
    }

    const updatePinStatus = async (comment) => {
        const commentDocRef = doc(db, 'ssv13_comments', `v${data.video_id}`, 'comments', comment.id);
        if (comment.isPinned === 0) {
            if (pinMessage.length < 4) {
                await updateDoc(commentDocRef, { isPinned: comment.isPinned === 0 ? 1 : 0 });
            } else {
                toast.warn("4 pinned comment section added fullfiled")
            }
        } else {
            await updateDoc(commentDocRef, { isPinned: 0 });
        }
        // const commentDocRef = doc(db, 'ssv13_comments', `v${data.video_id}`, 'comments', comment.id);
        // if (pinMessage.length < 4) {
        //     await updateDoc(commentDocRef, { isPinned: comment.isPinned === 0 ? 1 : 0 });
        // } else {
        //     await updateDoc(commentDocRef, { isPinned: 0 });
        // }
        subComment()
    }

    const removeComment = async (comment) => {
        try {
            const commentDocRef = doc(db, 'ssv13_comments', `v${data.video_id}`, 'comments', comment.id);
            await deleteDoc(commentDocRef);
            subComment();
        } catch (error) {
            console.error("Error deleting comment: ", error);
        }
    };

    const handleScroll = () => {
        if (scrolldivref.current.scrollTop >= 100) {
            setScrolled(true);
        } else {
            setScrolled(false);
        }
    };

    useEffect(() => {
        if (show) {
            fetchProducts()
        }
    }, [show])

    useEffect(() => {
        if (show) {
            setTimeout(() => {
                setScrolled(false)
                scrollToBottom()
            }, 1000);
        }
    }, [videoId, show])

    useEffect(() => {
        if (show && tab === "comments") {
            fetchData(data?.video_id);
        }
    }, [show, data.video_id, tab]);
    const topref = useRef()
    const scrollToTopOfElement = (elementId) => {

        topref?.current.scrollIntoView();

    };

    if (show) {
        return (
            <Modal
                show
                size="lg"
                aria-labelledby="contained-modal-title-vcenter"
                centered
                onHide={handleClose}
            >
                <div className='video-modal-ssv'>
                    <div className='video-modal-ssv-row'>
                        <div className='video-modal-ssv-col'>
                            <video className='modal_video_col' controls width="600" autoPlay loop muted>
                                <source src={data.url} type="video/mp4" />
                                Your browser does not support the video tag.
                            </video>
                        </div>
                        <div className='video-modal-ssv-col'>
                            <ul className="nav nav-tabs" id="responsive-tabs" role="tablist">
                                <li className="nav-item" role="presentation">
                                    <button className="nav-link active" id="product-tab" data-bs-toggle="tab" data-bs-target="#product" type="button" role="tab" aria-controls="video" aria-selected="true" onClick={() => setTab("products")}>Products</button>
                                </li>
                                <li className="nav-item" role="presentation">
                                    <button className="nav-link" id="comments-tab" data-bs-toggle="tab" data-bs-target="#comments" type="button" role="tab" aria-controls="Reports" aria-selected="false" onClick={() => setTab("comments")}>Comments</button>
                                </li>
                            </ul>
                            <div className="tab-content" id="responsive-tabs-content" style={{ height: "calc(100% - 42px)" }}>
                                <div className="tab-pane fade show active h-100" id="product" role="tabpanel" aria-labelledby="product-tab">
                                    {loadingProducts ? (
                                        <p className='text-muted mt-5 text-center'>Loading...</p>
                                    ) : (
                                        products.map((el, index) => {
                                            const currencySymbol = new Intl.NumberFormat(undefined, {
                                                style: "currency",
                                                currency: el.currencyName,
                                            })
                                                .formatToParts(0)
                                                .find((part) => part.type === "currency").value;

                                            return (
                                                <div className='p-2 mt-1 m-0 d-flex product_card' key={index}>
                                                    <img alt='productmini' src={el.image} className='rounded' style={{ height: "60px", width: "60px", border: "1px solid #aaa" }} />
                                                    <div className='ps-2 w-100'>
                                                        <h6 className='mb-0 product_title_video_model' title={el.title}>{el.title}</h6>
                                                        <p className='prouct_desc'>{currencySymbol}{parseInt(el.discountPrice).toLocaleString()} {el.discountPrice !== el.price && <span className='text-muted'><del>{currencySymbol}{parseInt(el.price).toLocaleString()}</del></span>} </p>
                                                    </div>
                                                </div>
                                            );
                                        })
                                    )}
                                </div>
                                <div className="tab-pane fade h-100" id="comments" role="tabpanel" aria-labelledby="comments-tab">
                                    <div className='comment-section-main-div-ssv'>
                                        <div className='section_comment_scroll' ref={scrolldivref} onScroll={handleScroll}>
                                            <div ref={topref}></div>
                                            <div className="pinned_section_closed" style={{ display: scrolled && pinMessage.length > 0 ? "" : "none" }} onClick={scrollToTopOfElement}>
                                                <p>
                                                    Pinned comment
                                                    <label className="JS-pinned-comment-counter-ssv13">{pinMessage.length}</label>
                                                </p>
                                                <p style={{ marginLeft: "auto" }}>
                                                    <svg width="13" height="13" viewBox="0 0 13 13" fill="none" xmlns="http://www.w3.org/2000/svg">
                                                        <path d="M12.8764 5.78136L11.9331 6.72402L11.4618 6.25269L8.63377 9.08069L8.16244 11.438L7.2191 12.3807L4.3911 9.55202L1.0911 12.852L0.148438 11.9094L3.44844 8.60936L0.619771 5.78136L1.56244 4.83802L3.92044 4.36669L6.74844 1.53869L6.2771 1.06736L7.21977 0.124023L12.8764 5.78136Z" fill="#334499"></path>
                                                    </svg>
                                                </p>
                                            </div>
                                            {pinMessage.length > 0 && (
                                                <div className='pin display-flex flex-column'>
                                                    <div id='pinnedcommentsection'>
                                                        {pinMessage.map((el) => (
                                                            <PinComment el={el} updatePinStatus={updatePinStatus} removeComment={removeComment} key={el.id} />
                                                        ))}
                                                    </div>
                                                </div>
                                            )}
                                            {comments.length > 0 ? (
                                                <div className='display-flex flex-column' id='unpinnedcommentsection' style={{ overflow: "auto" }}>
                                                    {comments.map((el) => (
                                                        <Comment editVideoComment={editVideoComment} el={el} updatePinStatus={updatePinStatus} removeComment={removeComment} key={el.id} />
                                                    ))}
                                                </div>
                                            ) : (
                                                <p className='text-muted text-center mt-5'>No comments</p>
                                            )}
                                            <div ref={chatEndRef}></div>

                                        </div>
                                        <div className='form-comment p-1 border-top'>
                                            <form className='d-flex' onSubmit={handleSubmit}>
                                                <input className='form-control mt-auto' max={250} maxLength={250} onChange={(e) => setMessage(e.target.value)} placeholder='Type something here..' type='text' />
                                                <button type='submit' className='btn btn-sm btn-dark ms-2' disabled={message.length === 0}>Send</button>
                                            </form>

                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </Modal>
        );
    }

    return null;
};

export default VideoModal;