import React, { useEffect, useState } from 'react';
import { Modal } from 'react-bootstrap';
import DataTable from 'react-data-table-component';
import "./ask.css"
import { BsQuestionCircleFill } from 'react-icons/bs';
import { format } from 'date-fns';
import axios from 'axios';
import { BiExport } from 'react-icons/bi';
import * as XLSX from 'xlsx';
import { saveAs } from 'file-saver';
import { RotatingLines } from 'react-loader-spinner';
const AskQuestionModel = ({ show, handleClose, data }) => {
    const [askQuestData, setAskQuestData] = useState([])
    const [pending, setPending] = React.useState(false);
    const columns = [
        {
            name: 'Video Title',
            selector: row => data.video_title,
        },
        {
            name: 'Name',
            selector: row => row.name,
        },
        {
            name: 'Date',
            selector: row => data.date,
        },
        {
            name: 'Mobile',
            selector: row => row.mobile,
        },
        {
            name: 'Message',
            selector: row => row.message,
        }
    ];

    async function fetchAskQuestionsData() {
        setPending(true)
        try {
            let requestBody = new FormData();
            requestBody.append("brand_id", data.brand_id);
            requestBody.append("start_date", format(data.startDate, 'yyyy-MM-dd'));
            requestBody.append("end_date", format(data.endDate, 'yyyy-MM-dd'));
            requestBody.append("video_id", data.video_id);
            const response = await axios.post(
                "https://bigleap.live/index.php/APIv1/ShortVideoAnalytics/getAskquestion",
                requestBody
            ).then((res) => {
                setAskQuestData(res.data.data)
                setPending(false)
            }).catch((errr) => {
                console.log(errr);
                setPending(false)
            })



        } catch (error) {
            setPending(false)
            throw error; // Rethrow the error if any occurs
        }
    }

    useEffect(() => {
        if (show) {
            fetchAskQuestionsData(data.date)
        }
    }, [data.date])
    const downloadExcel = () => {
        const worksheet = XLSX.utils.json_to_sheet(askQuestData);
        const workbook = XLSX.utils.book_new();
        XLSX.utils.book_append_sheet(workbook, worksheet, 'Sheet1');
        const excelBuffer = XLSX.write(workbook, { bookType: 'xlsx', type: 'array' });
        saveAs(new Blob([excelBuffer], { type: 'application/octet-stream' }), 'askQuestData.xlsx');
    };


    return (
        <>

            <Modal show={show} className='d-grid w-100 askquestion_modal ' size='lg' onHide={handleClose}>
                <Modal.Title className='p-3 pb-0'>Asked Questions <BsQuestionCircleFill style={{ cursor: "pointer" }} title='These are raised questions asked by viewers on video or product' /><button className='btn btn-sm btn-outline-dark float-end' onClick={() => downloadExcel()}><span className='mt-3'>Export</span> <BiExport size={25} /></button><hr /></Modal.Title>
                <Modal.Body className='p-3 pt-0'>
                    <DataTable
                        columns={columns}
                        data={askQuestData}
                        progressPending={pending}
                        progressComponent={<div className='p-3 m-4' >
                            <RotatingLines height={30} width={30} strokeColor="grey" />
                        </div>}

                    />
                </Modal.Body>

            </Modal>
        </>
    );
};

export default AskQuestionModel;
