import React, { useCallback, useEffect, useState } from 'react'
import DataTable from 'react-data-table-component';
import Dropdown from 'react-bootstrap/Dropdown';
import { DateRangePicker } from 'react-date-range'
import { FaFileCsv, FaFileExcel, FaFilePdf, FaSearch } from 'react-icons/fa';
import VideoModal from '../videomodal/VideoModal';
import * as XLSX from 'xlsx';
import { saveAs } from 'file-saver';
import jsPDF from 'jspdf';
import 'jspdf-autotable'; // Importing jspdf-autotable for table creation
import axios from 'axios';
import { useSelector } from 'react-redux';
import { format } from 'date-fns';
import AskQuestionModel from '../ask-question-modal/AskQuestionModel';
import { AiOutlinePlaySquare } from 'react-icons/ai';
import { RotatingLines, TailSpin } from 'react-loader-spinner';
import { BiExport } from 'react-icons/bi';

const Report = ({ state, setState }) => {
    const today = new Date();
    const yesterday = new Date(today);
    const [show, setShow] = useState(false);
    const [singleVideoData, setSingleVideoData] = useState([])
    const [askQuestionModelShow, setAskQuestionModelShow] = useState(false)
    const [askquestionData, setAskquestionData] = useState([])
    const [filteredData, setFilteredData] = useState([])
    const [filterText, setFilterText] = useState("")
    const [filtered, setFiltered] = useState(false)
    const handleClose = () => setShow(false);
    const [pending, setPending] = React.useState(false);
    const handleCloseAskQuestion = () => setAskQuestionModelShow(false);

    const authData = useSelector((state => state.auth.user))
    const [data, setData] = useState([])
    yesterday.setDate(today.getDate() - 1);
    // const [state, setState] = useState([
    //     {
    //         startDate: new Date(today.getTime() - 7 * 24 * 60 * 60 * 1000),
    //         endDate: yesterday,
    //         key: 'selection'
    //     }
    // ]);

    const [xstate, setxState] = useState([
        {
            startDate: new Date(today.getTime() - 7 * 24 * 60 * 60 * 1000),
            endDate: yesterday,
            key: 'selection'
        }
    ]);
    // useEffect(() => {
    //     return () => {
    //         setState([
    //             {
    //                 startDate: new Date(today.getTime() - 7 * 24 * 60 * 60 * 1000),
    //                 endDate: yesterday,
    //                 key: 'selection'
    //             }
    //         ])
    //     }
    // }, [])
    const [dropdownOpen, setDropdownOpen] = useState(false);
    const toggleDropdown = () => setDropdownOpen(!dropdownOpen);

    const handleApply = () => {
        // Your apply logic here
        setDropdownOpen(false); // Close the dropdown
        setState(xstate)
    };
    function convertSecondsToTime(seconds) {
        if (isNaN(seconds) || seconds < 0) {
            return "0";
        }

        const hours = Math.floor(seconds / 3600);
        const minutes = Math.floor((seconds % 3600) / 60);
        const remainingSeconds = seconds % 60;

        if (hours > 0) {
            return `${hours}hr ${minutes}min ${remainingSeconds}sec`;
        } else if (minutes > 0) {
            return `${minutes}min ${remainingSeconds}sec`;
        } else {
            return `${remainingSeconds}sec`;
        }
    }
    const myFun = () => {
        let total = 0
        let wtime = 0
        data && data.map((el) => {
            total += parseInt(el.total_views)
            wtime += parseInt(el.total_watchtime)

        })

    }
    const columns = [
        {
            // name: 'Sr',
            selector: row => row.index + 1,
            sortable: true,
            width: "4%"
        },
        {
            name: 'Video Title',
            selector: row => row.video_title,
            sortable: true,
            width: "22.2%",
            cell: (row) => {
                return <>
                    <p className='pt-4' title={row.video_title} style={{ padding: "2px", overflow: "hidden", whiteSpace: "nowrap", maxWidth: "280px", textOverflow: "ellipsis", cursor: "pointer" }} onClick={() => {
                        setSingleVideoData(row)
                        setShow(true)
                    }}>{row.video_title ? row.video_title : "no title"} </p >
                    <span onClick={() => {
                        setSingleVideoData(row)
                        setShow(true)
                    }} style={{ cursor: "pointer" }}>  <AiOutlinePlaySquare size={20} className='ms-1' /></span>
                </>
            },

        },
        {
            name: 'Total Views',
            selector: row => row.total_views,
            sortable: true,
            style: {
                backgroundColor: "rgba(255, 0, 0,.1)"
            }
        },
        {
            name: `Unique  Views`,
            selector: row => row.total_unique_views,
            sortable: true,
            style: {
                backgroundColor: "rgba(255, 0, 0,.1)"
            }
        },
        {
            name: 'Watch Time [Mins]',
            selector: row => row.total_watchtime,
            sortable: true,
            style: {
                backgroundColor: "rgba(255, 0, 0,.1)"
            },
            cell: (row) => {
                return (parseInt(row.total_watchtime) / 60).toFixed(2)
            }
        },
        {
            name: 'Avg. Watch Time [Secs]',
            selector: row => Math.round(row.average_watchtime),
            sortable: true,
            style: {
                backgroundColor: "rgba(255, 0, 0,.1)"
            },

        },

        {
            name: 'Add To Cart',
            selector: row => row.addtoCart,
            sortable: true,
            style: {
                backgroundColor: "rgba(0, 255, 71, .1)"
            }
        },
        {
            name: 'Buy Now',
            selector: row => row.buyNow,
            sortable: true,
            style: {
                backgroundColor: "rgba(0, 255, 71, .1)"
            }
        },
        {
            name: 'Ask Question',
            selector: row => row.askQuestion,
            sortable: true,
            style: {
                backgroundColor: "rgba(0, 255, 71, .1)"
            },
            cell: (row) => {
                return row.askQuestion > 0 ? <p onClick={() => {
                    setAskQuestionModelShow(true)
                    setAskquestionData(row)
                }} style={{ cursor: "pointer" }}><span className='badge_ask_count'>{row.askQuestion}</span></p> : <p style={{ padding: "3px 5px" }}>{row.askQuestion}</p>
            }
        },
        {
            name: 'Engage. Rate %',
            selector: row => row.engagement_rate === null ? `${"0" + "%"}` : `${Math.round(row.engagement_rate)}%`,
            sortable: true,
            style: {
                backgroundColor: "rgba(0, 255, 71, .1)"
            }
        },
        {
            name: 'CTR%',
            selector: row => `${Math.round(row.ctr_rate)}%`,
            sortable: true,
            style: {
                backgroundColor: "rgba(0, 255, 71, .1)"
            }
        },
    ]
    // const handleRowClicked = (row) => {
    //     setSingleVideoData(row)
    //     setShow(true)
    // }
    const CustomSortIconComponent = () => {
        return <>

            <div className='ms-3'>
                <svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M13.3333 2.6665V10.6665H15.3333L12.6667 13.9998L10 10.6665H12V2.6665H13.3333ZM8 11.9998V13.3332H2V11.9998H8ZM9.33333 7.33317V8.6665H2V7.33317H9.33333ZM9.33333 2.6665V3.99984H2V2.6665H9.33333Z" fill="#09121F" />
                </svg>
            </div>
        </>
    };

    const getData = useCallback(async () => {
        setPending(true)
        try {
            await axios.get(`https://goswirl.world:3001/getAnalyticsDataByBrandIdAndDate?brand_id=${authData?.brand_id}&start_date=${format(state[0].startDate, 'yyyy-MM-dd')}&end_date=${format(state[0].endDate, 'yyyy-MM-dd')}`).then((response) => {
                setData(response.data.resp)
                if (response.data) {
                    setPending(false)
                }
            }).catch((err) => {
                console.log(err);
                setPending(false)
            })
        } catch (error) {
            console.log(error);
        }
    }, [authData.brand_id, state])


    const filterData = (text) => {
        setFilterText(text)
        const res = data.filter(item => item.video_title && item.video_title.toLowerCase().includes(filterText.toLowerCase()));
        setFilteredData(res)

    }
    // const downloadExcel = () => {
    //     // Define your custom column headers
    //     const headers = ['Total Unique Views', 'Video Title', 'Brand Id', 'Video Id', 'Total Watch Time', 'Id', 'Url', 'Duration', 'Total Views', 'Date', 'Buy Now', 'Add To Cart', 'Ask Questions', 'Average Watch time', 'Engagement Rate', 'CTR'/* Add more titles as needed */];

    //     // Function to handle missing keys and set default value
    //     const handleMissingKey = (obj, key, defaultValue = '-') => {
    //         return obj.hasOwnProperty(key) ? obj[key] : defaultValue;
    //     };

    //     // Convert data to an array of arrays with headers as the first row
    //     const excelData = [headers, ...data.map(item => [
    //         handleMissingKey(item, 'total_unique_views'),
    //         handleMissingKey(item, 'video_title'),
    //         handleMissingKey(item, 'brand_id'), /* Add more fields as needed */
    //         handleMissingKey(item, 'video_id'), /* Add more fields as needed */
    //         handleMissingKey(item, 'total_watchtime'), /* Add more fields as needed */
    //         handleMissingKey(item, 'id'), /* Add more fields as needed */
    //         handleMissingKey(item, 'url'), /* Add more fields as needed */
    //         handleMissingKey(item, 'duration'), /* Add more fields as needed */
    //         handleMissingKey(item, 'total_views'), /* Add more fields as needed */
    //         handleMissingKey(item, 'date'), /* Add more fields as needed */
    //         handleMissingKey(item, 'buyNow'), /* Add more fields as needed */
    //         handleMissingKey(item, 'addtoCart'), /* Add more fields as needed */
    //         handleMissingKey(item, 'askQuestion'), /* Add more fields as needed */
    //         handleMissingKey(item, 'average_watchtime'), /* Add more fields as needed */
    //         handleMissingKey(item, 'engagement_rate'), /* Add more fields as needed */
    //         handleMissingKey(item, 'ctr'), /* Add more fields as needed */
    //     ])];

    //     // Convert data to worksheet
    //     const worksheet = XLSX.utils.aoa_to_sheet(excelData);

    //     // Create workbook and append the worksheet
    //     const workbook = XLSX.utils.book_new();
    //     XLSX.utils.book_append_sheet(workbook, worksheet, 'Sheet1');

    //     // Convert workbook to Excel buffer
    //     const excelBuffer = XLSX.write(workbook, { bookType: 'xlsx', type: 'array' });

    //     // Trigger download
    //     saveAs(new Blob([excelBuffer], { type: 'application/octet-stream' }), 'data.xlsx');
    // };


    const downloadExcel = () => {
        // Define your custom column headers
        const headers = ['Date', 'Video Title', 'Total Views', 'Unique Views', "Watch Time [Mins]", 'Avg Watch Time [Secs]', 'Buy Now', 'Add to Cart', 'Ask Question', 'Engagement %', 'CTR %'   /* Add more titles as needed */];

        // Function to handle missing keys and set default value
        const handleMissingKey = (obj, key, defaultValue = '-') => {
            return obj.hasOwnProperty(key) ? obj[key] : defaultValue;
        };

        // Define which fields to export
        const fieldsToExport = ['date', 'video_title', 'total_views', 'total_unique_views', 'total_watchtime', 'average_watchtime', 'buyNow', 'addtoCart', 'askQuestion', 'engagement_rate', 'ctr_rate'];

        // Convert data to an array of arrays with headers as the first row
        const modifiedData = data.map(item => {
            // Make changes to the 'video_title' key (example: convert to uppercase)
            const modifiedItem = { ...item, total_watchtime: (parseInt(item.total_watchtime) / 60).toFixed(2) };
            return fieldsToExport.map(field => handleMissingKey(modifiedItem, field));
        });

        const excelData = [headers, ...modifiedData];

        // Convert data to worksheet
        const worksheet = XLSX.utils.aoa_to_sheet(excelData);

        // Create workbook and append the worksheet
        const workbook = XLSX.utils.book_new();
        XLSX.utils.book_append_sheet(workbook, worksheet, 'Sheet1');

        // Convert workbook to Excel buffer
        const excelBuffer = XLSX.write(workbook, { bookType: 'xlsx', type: 'array' });

        // Trigger download
        saveAs(new Blob([excelBuffer], { type: 'application/octet-stream' }), 'data.xlsx');
    };



    const downloadCSV = () => {
        // Define your custom column headers
        const headers = ['Date', 'Video Title', 'Total Views', 'Unique Views', "Watch Time [Mins]", 'Avg Watch Time [Secs]', 'Buy Now', 'Add to Cart', 'Ask Question', 'Engagement %', 'CTR %'   /* Add more titles as needed */];
        const fieldsToExport = ['date', 'video_title', 'total_views', 'total_unique_views', 'total_watchtime', 'average_watchtime', 'buyNow', 'addtoCart', 'askQuestion', 'engagement_rate', 'ctr_rate'];

        // Function to handle missing keys and set default value
        const handleMissingKey = (obj, key, defaultValue = '-') => {
            return obj.hasOwnProperty(key) ? obj[key] : defaultValue;
        };

        // Convert data to CSV string
        let csvContent = headers.join(',') + '\n';
        data.forEach(item => {
            // Modify the 'video_title' key (example: convert to uppercase)
            const modifiedItem = { ...item, total_watchtime: (parseInt(item.total_watchtime) / 60).toFixed(2) };

            const row = [
                handleMissingKey(modifiedItem, fieldsToExport[0]),
                handleMissingKey(modifiedItem, fieldsToExport[1]),
                handleMissingKey(modifiedItem, fieldsToExport[2]),
                handleMissingKey(modifiedItem, fieldsToExport[3]),
                handleMissingKey(modifiedItem, fieldsToExport[4]),
                handleMissingKey(modifiedItem, fieldsToExport[5]),
                handleMissingKey(modifiedItem, fieldsToExport[6]),
                handleMissingKey(modifiedItem, fieldsToExport[7]),
                handleMissingKey(modifiedItem, fieldsToExport[8]),
                handleMissingKey(modifiedItem, fieldsToExport[9]),
                handleMissingKey(modifiedItem, fieldsToExport[10]),
            ];
            csvContent += row.join(',') + '\n';
        });

        // Create Blob from CSV string
        const csvBlob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' });

        // Trigger download
        saveAs(csvBlob, 'data.csv');
    };



    const downloadPDF = () => {
        const doc = new jsPDF('l', 'pt', 'a4', true); // Landscape orientation

        // Define your custom column headers
        const headers = ['Date', 'Video Title', 'Total Views', 'Unique Views', "Watch Time [Mins]", 'Avg Watch Time [Secs]', 'Buy Now', 'Add to Cart', 'Ask Question', 'Engagement %', 'CTR %'   /* Add more titles as needed */];
        const fields = ['date', 'video_title', 'total_views', 'total_unique_views', 'total_watchtime', 'average_watchtime', 'buyNow', 'addtoCart', 'askQuestion', 'engagement_rate', 'ctr_rate'];

        // Function to handle missing keys and set default value
        const handleMissingKey = (obj, key, defaultValue = '-') => {
            return obj.hasOwnProperty(key) ? obj[key] : defaultValue;
        };

        // Convert data to an array of arrays with custom column headers
        const tableData = [headers];
        data.forEach(item => {
            // Modify the 'video_title' key (example: convert to uppercase)
            const modifiedItem = { ...item, total_watchtime: (parseInt(item.total_watchtime) / 60).toFixed(2) };

            const row = fields.map(field => handleMissingKey(modifiedItem, field));
            tableData.push(row);
        });

        doc.autoTable({
            head: [headers],
            body: tableData.slice(1), // Exclude headers from body data
            headStyles: {
                fillColor: [238, 238, 119],
                textColor: [0, 0, 0]
            },
            styles: { cellPadding: 1, fontSize: 5 },
            columnStyles: { 0: { cellWidth: 'auto' } },
            margin: { top: 20 },
            startY: 20
        });
        doc.save('data.pdf');
    };




    useEffect(() => {
        if (filterText !== "") {
            setFiltered(true)
        } else {
            setFiltered(false)
        }
    }, [filterText])

    useEffect(() => {
        getData()
        myFun()
    }, [state, getData])


    useEffect(() => {
        setxState(state)
    }, [])


    return (
        <>
            <div className='d-flex'>
                <div className='ms-auto d-flex align-items-center mb-3' >
                    <div className="dropdown d-flex">
                        <div className="input-group input-group me-1">
                            <span className="input-group-text border-end-0 border-dark" >
                                <FaSearch />
                            </span>
                            <input
                                type="search"
                                className="form-control border-start-0 border-dark"
                                name='search'
                                value={filterText}
                                placeholder='Search video...'
                                onChange={(e) => filterData(e.target.value)}


                            // onChange={(e) => handleInputChange("mobile", e.target.value)}
                            />

                        </div>

                        {/* <input className='form-control video_search border-dark me-1 text-dark' placeholder='Search video...' type='search' /> */}
                        <Dropdown show={dropdownOpen} onToggle={toggleDropdown}>
                            {window.innerWidth > 556 ? <Dropdown.Toggle id="dropdown-basic" className='bg-transparent text-dark border-dark'>
                                <span>
                                    {state[0].endDate !== null ? `${state[0].startDate.toString().slice(4, 15)} to ${state[0].endDate.toString().slice(4, 15)}` : "Filter by date"}
                                </span>
                            </Dropdown.Toggle> : <Dropdown.Toggle id="dropdown-basic" className='bg-transparent text-dark border-dark'>
                                <span>
                                    {state[0].endDate !== null ? `${state[0].startDate.getFullYear().toString().slice(2, 4)}/${state[0].startDate.getFullYear().toString().slice(2, 4)}/${state[0].startDate.getFullYear().toString().slice(2, 4)} - ${state[0].startDate.getFullYear().toString().slice(2, 4)}/${state[0].startDate.getFullYear().toString().slice(2, 4)}/${state[0].startDate.getFullYear().toString().slice(2, 4)}` : "Filter by date"}
                                </span>
                            </Dropdown.Toggle>}


                            <Dropdown.Menu>
                                <DateRangePicker
                                    editableDateInputs={false}
                                    onChange={item => setxState([item.selection])}
                                    moveRangeOnFirstSelection={true}
                                    ranges={xstate}
                                    months={2}
                                    direction='horizontal'
                                // maxDate={yesterday}
                                // minDate={authData?.brand_id == "236633565" ? new Date(2024, 2, 15) : new Date(2024, 3, 5)}
                                />
                                <div className='w-100'>
                                    <button id="dropdown-basic" className='btn btn-sm btn-primary float-end me-3' onClick={handleApply}>Done</button>
                                    <button id="dropdown-basic" className='btn btn-sm btn-outline-primary float-end me-3' onClick={() => {
                                        setxState([
                                            {
                                                startDate: new Date(today.getTime() - 7 * 24 * 60 * 60 * 1000),
                                                endDate: yesterday,
                                                key: 'selection'
                                            }
                                        ])
                                    }}>Reset</button>

                                </div>
                            </Dropdown.Menu>
                        </Dropdown>
                        <Dropdown className='ms-1'>
                            {window.innerWidth > 556 ? <Dropdown.Toggle className='bg-transparent text-dark border-dark'>
                                Export
                            </Dropdown.Toggle> : <Dropdown.Toggle className='bg-transparent text-dark border-dark'>
                                <BiExport />
                            </Dropdown.Toggle>}


                            <Dropdown.Menu>
                                <Dropdown.Item onClick={downloadCSV}><FaFileCsv color='gray' /> Export as CSV </Dropdown.Item>
                                <Dropdown.Item onClick={downloadExcel}><FaFileExcel color='green' /> Export as XLS</Dropdown.Item>
                                <Dropdown.Item onClick={downloadPDF}><FaFilePdf color='red' /> Export as PDF</Dropdown.Item>
                            </Dropdown.Menu>
                        </Dropdown>
                    </div>
                </div>
            </div >
            <div style={{ width: "100%", overflow: "auto", height: "auto" }}>
                <div style={{ border: "1px solid #E0E0E0", minWidth: "1500px" }}>
                    <DataTable
                        columns={columns}
                        data={filtered ? filteredData?.map((item, index) => ({ ...item, index })) : data?.map((item, index) => ({ ...item, index }))}
                        subHeader
                        progressPending={pending}
                        pagination
                        progressComponent={<div className='p-3 m-4' >
                            <RotatingLines height={30} width={30} strokeColor="grey" />
                        </div>}
                        sortIcon={<CustomSortIconComponent />}
                        highlightOnHover
                        // sortIconRenderer={() => <CustomSortIconComponent />}
                        // onRowClicked={handleRowClicked}
                        className="custom-datatable"

                        subHeaderComponent={
                            <>
                                <div className='d-flex w-100 table_header_row' >
                                    <div className='table_blank_column'></div>
                                    <div className='table_higher_column' >
                                        <div className='d-flex text-center'>
                                            <div className='perfomance_column'>Performance</div>
                                            <div className='engagement_column' >Engagement</div>
                                        </div>
                                    </div>
                                </div>
                            </>}
                    />

                    {/* code for server side rendering  */}
                    {/* <DataTable
title="My Data Table"
columns={columns}
data={data}
pagination={true}
paginationServer={true}
paginationTotalRows={data.length} // This should be total rows count from your server
onChangePage={async (page, totalRows) => {
// Fetch data for the new page from the server
setLoading(true);
const response = await fetch(`your-api-endpoint?page=${page}&pageSize=${totalRows}`);
const responseData = await response.json();
setData(responseData);
setLoading(false);
}}
progressPending={loading}
/> */}
                </div>
            </div>
            <VideoModal show={show} handleClose={handleClose} data={singleVideoData} />
            <AskQuestionModel show={askQuestionModelShow} handleClose={handleCloseAskQuestion} data={{ ...askquestionData, ...state[0] }} />

        </>
    )
}

export default Report